// 城市选择字母列表
import {getCity} from "@/api/home/city.js"
export default{
	data(){
		return{
			letters:[],
		}
	},
	created() {
		this.getCitys()
	},
	methods:{
		async getCitys(){
			await getCity().then((res)=>{
				this.letters = res.data
				//console.log(res)
			})
		},
		handleLetter(key){
			this.$store.commit("changeLetter",key) 
		}
	}
}
